import { Component,ViewChild } from '@angular/core';
import { IonicPage, NavController, NavParams,Events } from 'ionic-angular';
import { SignaturePad } from 'angular2-signaturepad/signature-pad';//引入手写板
import { PhoneDeviceProvider } from '../../../providers/phone-device';
// import { ScreenOrientation } from '@ionic-native/screen-orientation/ngx';
import { StatusBar } from '@ionic-native/status-bar';

// import { ScreenOrientation } from '@ionic-native/screen-orientation';
/**
 * Generated class for the SignaturepadPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */
@IonicPage()
@Component({
  selector: 'page-signaturepad',
  templateUrl: 'signaturepad.html',
})
export class SignaturepadPage {
  @ViewChild(SignaturePad) signaturePad: SignaturePad;
  signatureImage: string; //定义类型
  // public userList:any=[];
  signaturePadOptions: Object = {
    'minWidth': 2,
    'canvasWidth': 1120,
    'canvasHeight': 320
  };
  public typeindex;
  constructor(public navCtrl: NavController,public navParams: NavParams,
    // public screenOrientation: ScreenOrientation,
    public statusBar: StatusBar, 
    public phoneDevice: PhoneDeviceProvider,public events:Events) {
      this.typeindex=this.navParams.get('typeindex');
      this.statusBar.styleBlackTranslucent();
      this.statusBar.backgroundColorByHexString('rgba(0,0,0,0)');
      // console.log(window.screen)
    // this.phoneDevice.phoneBacktoPop(this.navCtrl);
  }
  ionViewDidLoad() {
    console.log(JSON.stringify(window.screen))
    // this.screenOrientation.lock('landscape');
}
  ionViewWillEnter(){
    // console.log(JSON.stringify(window.screen.orientation))
    (window as any).screen.orientation.lock('landscape'); 
        // window.screen.orientation.lock('landscape'); 
        // this.screenOrientation.lock('landscape'); 
    }
    ionViewWillLeave(){
      (window as any).screen.orientation.lock('portrait');
      // this.screenOrientation.lock('portrait');
      // this.screenOrientation.unlock();
    }
  ngAfterViewInit() {
    this.signaturePad.clear();
    this.canvasResize();
  }
  // 清除模板
  drawClear() {
    this.signaturePad.clear();
  }
  canvasResize() {
    let canvas = document.querySelector('canvas');
    // console.log(222,JSON.stringify(canvas.offsetWidth))
    // console.log(111,JSON.stringify(canvas.offsetHeight))
    // this.signaturePad.set('canvasWidth', canvas.offsetHeight);
    this.signaturePad.set('canvasHeight', canvas.offsetHeight - 100);
  }
  // 完成生成图片
  drawComplete(sign) {
    this.signatureImage = this.signaturePad.toDataURL();
    // console.log(this.signatureImage);
    var signdata =  {
      img:  this.signatureImage ,
      typeind:  this.typeindex ,
    };
    this.navCtrl.pop();
    this.events.publish('signatureImage:signatureImageinfo',signdata,Date.now());
  }
  // commitBtn(){
  //   this.navCtrl.pop();
  //   this.events.publish('signatureImage:signatureImageinfo',this.signatureImage,Date.now());
  // }

}



